<template>
  <div class="system">
    <div class="main_module_body" v-show="currentLang == 'zh'">
      <!-- <el-row :style="ListBackground" :gutter="0"> -->
      <el-row :gutter="0">
        <el-col :span="2">
          <p>&nbsp;</p>
        </el-col>
        <el-col class="modulelist" :span="20">
          <div
            class="module_content"
            v-for="(item,index) in module.slice(0,3)"
            :key="index + 'aasda' "
          >
            <div class="module_info">
              <!--  -->
              <el-row :gutter="0">
                <el-col :span="8">
                  <div class="avatar">
                    <img :style="{width:'120px',height:'200px'}" :src="item.avatar">
                  </div>
                </el-col>
                <el-col :span="2">
                  <p>&nbsp;</p>
                </el-col>
                <el-col :span="14">
                  <!--  -->
                  <el-row :gutter="0">
                    <el-col :span="14">
                      <p class="module_product_name">{{item.Name}}</p>
                    </el-col>
                    <el-col :span="5">
                      <p>&nbsp;</p>
                    </el-col>
                    <!-- <el-col :span="5">
                      <span class="star">★</span>
                    </el-col>-->
                    <span
                      v-show="item.IfCollect"
                      @click="ifStar(item.ID)"
                      :style="{color: style.starColorOK}"
                      class="star"
                    >★</span>
                    <span
                      v-show="!item.IfCollect"
                      @click="ifStar(item.ID)"
                      :style="{color: style.starColorNO}"
                      class="star"
                    >★</span>
                  </el-row>
                  <!--  -->
                  <p>性别：{{item.Sex}}</p>
                  <p>年龄：{{item.Age}}</p>
                  <p>婚否：{{item.MaritalStatus}}</p>
                  <p>经验：{{item.WorkExp}}</p>
                  <p>国籍：{{item.Nationality}}</p>
                  <p>
                    海外工作经验：
                    <span
                      v-for="(itemChilren,index) in item.OverseasWorkplace"
                      :key="index + 'c' "
                    >
                      <span>{{itemChilren}}&nbsp;&nbsp;</span>
                    </span>
                  </p>
                  <p>
                    女佣星级：
                    <span
                      v-for="(itemChildren,index) in item.TheMaidStar"
                      :key="index + 'aasda' "
                    >
                      <span class="level">★</span>
                    </span>
                  </p>
                </el-col>
              </el-row>
              <!--  -->
            </div>
          </div>

          <!--  -->
          <div
            class="module_content"
            v-for="(item2,index2) in module.slice(3,6)"
            :key="index2 + 'aasda2' "
          >
            <div class="module_info">
              <!--  -->
              <el-row :gutter="0">
                <el-col :span="8">
                  <div class="avatar">
                    <img :style="{width:'120px',height:'200px'}" :src="item2.avatar">
                  </div>
                </el-col>
                <el-col :span="2">
                  <p>&nbsp;</p>
                </el-col>
                <el-col :span="14">
                  <!--  -->
                  <el-row :gutter="0">
                    <el-col :span="14">
                      <p class="module_product_name">{{item2.Name}}</p>
                    </el-col>
                    <el-col :span="5">
                      <p>&nbsp;</p>
                    </el-col>
                    <!-- <el-col :span="5">
                      <span class="star">★</span>
                    </el-col>-->
                    <span
                      v-show="item2.IfCollect"
                      @click="ifStar(item2.ID)"
                      :style="{color: style.starColorOK}"
                      class="star"
                    >★</span>
                    <span
                      v-show="!item2.IfCollect"
                      @click="ifStar(item2.ID)"
                      :style="{color: style.starColorNO}"
                      class="star"
                    >★</span>
                  </el-row>
                  <!--  -->
                  <p>性别：{{item2.Sex}}</p>
                  <p>年龄：{{item2.Age}}</p>
                  <p>婚否：{{item2.MaritalStatus}}</p>
                  <p>经验：{{item2.WorkExp}}</p>
                  <p>国籍：{{item2.Nationality}}</p>
                  <p>
                    海外工作经验：
                    <span
                      v-for="(itemChilren,index) in item2.OverseasWorkplace"
                      :key="index + 'c' "
                    >
                      <span>{{itemChilren}}&nbsp;&nbsp;</span>
                    </span>
                  </p>
                  <p>
                    女佣星级：
                    <span
                      v-for="(itemChildren,index) in item2.TheMaidStar"
                      :key="index + 'aasda' "
                    >
                      <span class="level">★</span>
                    </span>
                  </p>
                </el-col>
              </el-row>
              <!--  -->
            </div>
          </div>
        </el-col>
        <el-col :span="2">
          <p>&nbsp;</p>
        </el-col>
      </el-row>
    </div>
  </div>
</template>


<script>
import { mapGetters } from "vuex";

export default {
  name: "SystemRecommended",
  computed: {
    ...mapGetters(["currentLang", "module"])
  },
  data() {
    return {
      ListBackground: {
        backgroundImage: "url(" + require("@/assets/newpic11.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100%"
      },
      style: {
        starColorOK: "#3081FD",
        starColorNO: "#EDF0F9"
        // starColor
      }
    };
  },
  methods: {
    ifStar(ID) {
      console.log("~~~~ifStar");
      let vm = this;
      vm.$store
        .dispatch("update_ifCollectByID", ID)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
    }
  }
};
</script>

<style>
.system {
  width: 100%;
  max-width: 2000px;
  min-width: 1200px;
  background: #ffffff;
  padding: 60px 0px;
}
.system .main_module_body {
  width: 1440px;
  margin: 0 auto;
  overflow: hidden;
  margin-bottom: 20px;
}
.system .module_content {
  width: 30%;
  /* margin: 50px 40px 0px 50px; */
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  float: left;
  background: #fff;
}

.system .module_product_name {
  font-size: 22px;
  margin: 10px 0px;
}

.system .star {
  font-size: 20px;
  color: #3081fd;
  display: inline-block;
}
.system .level {
  font-size: 20px;
  color: #ffc618;
  display: inline-block;
}
/* ----new */

.system .module_content .avatar {
  text-align: center;
  /* margin-top: 30px; */
}
.system .module_content .avatar > img {
}
.system .module_info {
  /* margin-left: 25px; */
  padding: 15px;
  border: 1px solid #dadada;
}
.system .module_info p {
  margin: 5px 0px;
}
</style>

<style scoped>
</style>



