<template>
  <div class="view starmaid common">
    <el-image :style="{width:'100%'}" :src="style.src_pic"></el-image>
    <el-row class="myoption" :gutter="0">
      <el-col :span="7">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="4">
        <span class="mytitle" :style="{color:fontColorA}" @click="ChooseItem('1')">我的星标</span>
      </el-col>
      <el-col :span="2">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="4">
        <span class="mytitle" :style="{color:fontColorB}" @click="ChooseItem('2')">系统推荐</span>
      </el-col>
      <el-col :span="7">
        <p>&nbsp;</p>
      </el-col>
    </el-row>
    <com-mystar v-show="ItemDoor"></com-mystar>
    <com-systemrecommended v-show="!ItemDoor"></com-systemrecommended>
    <div class="body_bottom color_bg"></div>
  </div>
</template>

<script>
// import "@/styles/filipinomaid.css";
import "@/styles/common.css";

import MyStart from "./components/MyStar";
import SystemRecommended from "./components/SystemRecommended";

export default {
  name: "StarMaid",
  components: {
    "com-mystar": MyStart,
    "com-systemrecommended": SystemRecommended
  },
  data() {
    return {
      style: {
        src_pic: require("@/assets/maid/pic (4).png")
      },
      ItemDoor: true,
      fontColorA: "#3081FD",
      fontColorB: "#868686"
    };
  },
  mounted() {},
  methods: {
    ChooseItem(num) {
      if (num == "1") {
        this.ItemDoor = true;
        this.fontColorA = "#3081FD";
        this.fontColorB = "#868686";
      } else if (num == "2") {
        this.ItemDoor = false;
        this.fontColorA = "#868686";
        this.fontColorB = "#3081FD";
      }
    }
  }
};
</script>

<style scoped>
</style>

<style>
.starmaid {
  background: #ffffff;
}
.starmaid .mytitle {
  font-size: 30px;
}
.starmaid .myoption{
  margin-top: 40px;
}
</style>



